private const val link = "https://n8n-ext.onrender.com/webhook/4354e816-c3a3-458d-8185-7f043bf7e2bf"

@OptIn(InternalAPI::class)
@Composable
fun fryInstructions() {
    Surface {
        Column(
            modifier = Modifier
                .fillMaxSize()
                .padding(top = 16.dp),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {

            Row {
                BlueButton(text = "RU", onClick = { })
                BlueButton(text = "ENG", onClick = { })
                BlueButton(text = "UA", onClick = { })
            }
            Spacer(Modifier.height(64.dp))
            Column {
                DropdownButton(
                    title = "Aspect Ratio",
                    leftIconRes = Res.drawable.main_aspect_ratio,
                    dropDownValues = persistentListOf(
                        "- - - - -",
                        "1:1", "5:4", "3:2", "3:2", "7:4",
                        "16:9", "2:1", "9:16", "1:2",
                    ),
                    onValueChanged = { },
                )
                DropdownButton(
                    title = "Styleflow",
                    leftIconRes = Res.drawable.main_styleflow,
                    dropDownValues = persistentListOf(
                        "- - - - -",
                        "raw"
                    ),
                    onValueChanged = { }
                )
            }

            val localScope = rememberCoroutineScope()
            BlueButton(
                text = "Test request",
                onClick = {
                    localScope.launch {
                        val client = HttpClient()
                        val response = client.get(link) {
                            url {
                                parameters.apply {
                                    append("auth_date", webApp.initDataUnsafe.authDate.toString())
                                    webApp.initDataUnsafe.hash?.let { append("hash", it) }
                                    webApp.initDataUnsafe.queryId?.let { append("query_id", it) }
                                    append("user", getUser())
                                }
                            }
                            accept(ContentType.Application.Json)
                            method = HttpMethod.Get
                        }
//                        window.alert(
//                            response.body<String>() + " , " + response.status + " , " + response.body<Any?>()
//                                .toString()
//                        )

                        val app: AppJs = js("window.Telegram.WebApp")
                        window.alert(
                            "query_id: ${app.initDataUnsafe.query_id}"
                        )
                    }
                }
            )
        }
    }
}


private external class AppJs {
    @JsName("initDataUnsafe")
    val initDataUnsafe: AppInitData
}

private external class AppInitData {
    @JsName("query_id")
    val query_id: String
}