package fry.lab.app.data

import com.kirillNay.telegram.miniapp.webApp.webApp
import kotlinx.serialization.Serializable
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json

@Serializable
internal data class InitDataUserDto(
    val id: Int,
//    val is_bot: Boolean?,
    val first_name: String,
    val last_name: String?,
    val username: String?,
    val language_code: String?,
//    val is_premium: Boolean?,
//    val added_to_attachment_menu: Boolean?,
//    val allows_write_to_pm: Boolean?,
//    val photo_url: String?
)

internal fun getUser(): String = webApp.initDataUnsafe.user?.let { user ->
    Json.encodeToString(
        InitDataUserDto(
            id = user.id,
//            is_bot = user.isBot,
            first_name = user.firstName,
            last_name = user.lastName,
            username = user.username,
            language_code = user.languageCode,
//            is_premium = user.isPremium,
//            added_to_attachment_menu = user.addedToAttachmentMenu,
//            allows_write_to_pm = user.allowsWriteToPm,
//            photo_url = user.photoUrl
        )
    )
}.orEmpty()

